.choice-group {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  > .choice-item {
    position: relative;
    float: left;
    display: inline-block;
    width: @choice-item-width;
    height: @choice-item-height;
    line-height: @choice-item-height;
    text-align: center;
    color: @choice-item-color;
    border: 1px solid @border-color;
    background-color: @choice-item-bg;
    box-sizing: content-box;
    cursor: pointer;
    span {
      line-height: @choice-item-height;
    }
    &.active {
      color: @choice-item-active-color;
      background-color: @choice-item-active-bg;
    }
    + .choice-item {
      border-left: none;
    }
  }
}
