.tree-wrapper {
  position: relative;
}

.tree {
  position: relative;
  width: @tree-width;
  border: 1px solid @border-color;
  box-sizing: content-box;
  .tree-header {
    position: relative;
    padding: 0 @tree-header-h-padding;
    height: @tree-header-height;
    font-size: 0;
    line-height: (@tree-header-height / 2);
    background-color: @tree-header-bg;
    border-bottom: 1px solid @border-color;
    .tree-operation {
      display: inline-block;
      width: @tree-header-item-width;
      text-align: center;
      font-size: 12px;
    }
    .tree-body-toggle {
      position: absolute;
      bottom: 10px;
      right: 10px;
      font-size: 12px;
      line-height: 1;
      cursor: pointer;
    }
  }
  .tree-body {
    padding: 0;
    margin: 0;
    font-size: 12px;
    list-style-type: none;
    .tree-item {
      position: relative;
      width: @tree-body-item-width;
      line-height: @tree-body-item-height;
      border-bottom: 1px solid @border-color;
      background-color: @tree-body-item-bg;
      &.open,
      &:hover {
        background-color: @tree-body-item-active-bg;
        a {
          text-decoration: none;
        }
      }
      > a {
        display: block;
        padding-left: @tree-body-item-l-padding;
        padding-right: @tree-body-item-r-padding;
        color: @text-dark;
        border: none;
        line-height: inherit;
        &:hover {
          text-decoration: none;
        }
      }
      .dropdown-menu {
        left: (@tree-body-item-width + 1);
        top: -1px;
        margin: 0;
        border-color: @border-color;
        border-left: none;
        border-bottom: none;
        > li {
          padding: 0;
          > a {
            text-align: left;
          }
        }
      }
    }
  }
  .tree-footer {
    @padding-v: (@tree-width - @tree-footer-message-box-width) / 2;
    position: relative;
    padding: 60px 6px 5px 6px;
    .message-box {
      padding: 0 8px;
      line-height: 36px;
      color: @text-dark;
      font-size: 12px;
      border: 1px solid @border-color;
      background-color: @tree-footer-message-box-bg;
      cursor: pointer;
    }
    .tree-toggle {
      position: absolute;
      right: -20px;
      top: 20px;
      cursor: pointer;
    }
  }
  &.open {
    .tree-toggle {
      right: 0;
    }
  }
}
