.notifier {
  position: relative;
  display: inline-block;
  width: @notifier-width;
  height: @notifier-height;
  line-height: @notifier-height;
  text-align: center;
  color: @notifier-color;
  border: 1px solid @border-color;
  background-color: @notifier-bg;
  cursor: pointer;
  box-sizing: content-box;
  &.active {
    color: @notifier-active-color;
    background-color: @notifier-active-bg;
  }
}
