//
// Modals
// --------------------------------------------------
// .modal-open      - body class for killing the scroll
// .modal           - container to scroll within
// .modal-dialog    - positioning shell for the actual modal
// .modal-content   - actual modal w/ bg and corners and shit
// Kill the scroll on the body
.modal-open {
  overflow: hidden;
}

// Container that the modal scrolls within
.modal {
  display: none;
  overflow: hidden;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: @zindex-modal;
  -webkit-overflow-scrolling: touch;
  // Prevent Chrome on Windows from adding a focus outline. For details, see
  // https://github.com/twbs/bootstrap/pull/10951.
  outline: 0;
  // When fading in the modal, animate it to slide down
  &.fade .modal-dialog {
    .translate(0, -25%);
    .transition-transform(~"0.3s ease-out");
  }
  &.in .modal-dialog {
    .translate(0, 0)
  }
}

.modal-open .modal {
  overflow-x: hidden;
  overflow-y: auto;
}

// Shell div to position the modal with bottom padding
.modal-dialog {
  position: relative;
  width: auto;
  margin: 10px;
}

// Actual modal
.modal-content {
  position: relative;
  background-color: @modal-content-bg;
  border: 1px solid @modal-content-fallback-border-color; //old browsers fallback (ie8 etc)
  border: 1px solid @modal-content-border-color;
  .box-shadow(0 3px 9px rgba(0, 0, 0, .5));
  background-clip: padding-box;
  // Remove focus outline from opened modal
  outline: 0;
}

// Modal background
.modal-backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: @zindex-modal-background;
  background-color: @modal-backdrop-bg;
  // Fade for backdrop
  &.fade {
    .opacity(0);
  }
  &.in {
    .opacity(@modal-backdrop-opacity);
  }
}

// Modal header
// Top section of the modal w/ title and dismiss
.modal-header {
  padding: 0 @modal-title-h-padding;
  line-height: @modal-header-height;
  background-color: @theme-color;
  color: @text-white;
  // border-bottom: 1px solid @modal-header-border-color;
  &:extend(.clearfix all);
}

// Close icon
.modal-header .close {
  float: right;
  font-size: 30px;
  cursor: pointer;
  // margin-top: -2px;
}

// Title text within header
.modal-title {
  font-size: 14px;
  margin: 0;
  line-height: @modal-title-line-height;
}

// Modal body
// Where all modal content resides (sibling of .modal-header and .modal-footer)
.modal-body {
  @spacing: 25px;
  position: relative;
  padding: @modal-inner-v-padding @modal-inner-h-padding;
  .form-group {
    margin-bottom: @spacing;
    text-align: center;
    .control-label {
      font-weight: normal;
      width: 80px;
    }
    .form-control-wrapper {
      position: relative;
      display: inline-block;
      .form-control {
        display: inline-block;
        margin-right: 40px;
        width: 200px;
      }
      .text-danger {
        position: absolute;
        top: @input-height-base;
        left: @input-h-padding;
        font-size: 12px;
        line-height: @spacing;
      }
    }
  }
}

// Footer (for actions)
.modal-footer {
  @content-height: (@btn-lg-height + 2);
  @v-padding: ((@modal-footer-height - @content-height) / 2);
  @h-padding: 92px;
  padding: @v-padding @h-padding;
  text-align: center; // right align buttons
  border-top: 1px solid @modal-footer-border-color;
  background-color: @back-grey;
  &:extend(.clearfix all); // clear it in case folks use .pull-* classes on buttons
  // Properly space out buttons
  .btn + .btn {
    margin-left: 5px;
    margin-bottom: 0; // account for input[type="submit"] which gets the bottom margin like all other inputs
  }
  // but override that for button groups
  .btn-group .btn + .btn {
    margin-left: -1px;
  }
  // and override it for block buttons as well
  .btn-block + .btn-block {
    margin-left: 0;
  }
}

// Measure scrollbar width for padding body during modal show/hide
.modal-scrollbar-measure {
  position: absolute;
  top: -9999px;
  width: 50px;
  height: 50px;
  overflow: scroll;
}

// Scale up the modal
@media (min-width: @screen-sm-min) {
  // Automatically set modal's width for larger viewports
  .modal-dialog {
    width: @modal-md;
    margin: 30px auto;
  }
  .modal-content {
    .box-shadow(0 5px 15px rgba(0, 0, 0, .5));
  }
  // Modal sizes
  .modal-sm {
    width: @modal-sm;
  }
}

@media (min-width: @screen-md-min) {
  .modal-lg {
    width: @modal-lg;
  }
}
